<!-- src/components/Sidebar.vue -->
<template>
  <div class="sidebar-container" :class="{ 'sidebar-open': isOpen, 'toc-open': isTocOpen }">
    <div class="sidebar" ref="sidebarRef">
      <div v-if="isTocOpen" class="toc-content">
        <TableOfContents :toc="tableOfContents" @section-clicked="handleSectionClick" />
      </div>
      <nav v-else>
        <ul>
          <li v-for="item in menuItems" :key="item.path">
            <router-link :to="item.path" exact-active-class="active" @click="handleItemClick">
              <font-awesome-icon :icon="item.icon" class="nav-icon" />
              {{ item.name }}
            </router-link>
          </li>
        </ul>
      </nav>
    </div>
  </div>
</template>



<script>
import { computed, watch, ref, onMounted, onUnmounted } from 'vue'
import { useStore } from 'vuex'
import { useRoute } from 'vue-router'
import TableOfContents from './TableOfContents.vue'

export default {
  name: 'SidebarMenu',
  components: { TableOfContents },
  setup() {
    const store = useStore()
    const route = useRoute()
    const sidebarRef = ref(null)

    const isOpen = computed(() => store.state.content.sidebarOpen)
    const isTocOpen = computed(() => store.state.content.tocSidebarOpen)
    const isTopBarHidden = computed(() => store.state.content.topBarHidden)
    const tableOfContents = computed(() => store.state.content.tableOfContents)

    const handleOutsideClick = (event) => {
      if (sidebarRef.value && !sidebarRef.value.contains(event.target) && !event.target.closest('.menu-button') && !event.target.closest('.toc-button')) {
        store.dispatch('content/setSidebarOpen', false)
        store.dispatch('content/setTocSidebarOpen', false)
      }
    }

    onMounted(() => {
      document.addEventListener('click', handleOutsideClick)
    })

    onUnmounted(() => {
      document.removeEventListener('click', handleOutsideClick)
    })

    const handleItemClick = () => {
      if (window.innerWidth <= 768) {
        store.dispatch('content/setSidebarOpen', false)
      }
    }

    const handleSectionClick = (id) => {
      const element = document.getElementById(id)
      if (element) {
        const topBarHeight = isTopBarHidden.value ? 0 : parseInt(getComputedStyle(document.documentElement).getPropertyValue('--topbar-height'))
        const offset = topBarHeight + 16
        const elementPosition = element.getBoundingClientRect().top
        const offsetPosition = elementPosition + window.pageYOffset - offset

        window.scrollTo({
          top: offsetPosition,
          behavior: 'smooth'
        })
      }
      store.dispatch('content/setTocSidebarOpen', false)
    }

    const menuItems = [
      { name: 'About', path: '/about', icon: 'user-circle' },
      { name: 'Books', path: '/books', icon: 'book' },
      { name: 'Writing', path: '/blog', icon: 'pen' },
      { name: 'Physical', path: '/activities', icon: 'dumbbell' },
      { name: 'Contact', path: '/contact', icon: 'envelope' }
    ]

    watch(() => route.path, () => {
      store.dispatch('content/setTocSidebarOpen', false)
    })

    return {
      isOpen,
      isTocOpen,
      handleItemClick,
      handleSectionClick,
      menuItems,
      tableOfContents,
      sidebarRef
    }
  }
}
</script>

<style scoped>
.sidebar-container {
  position: fixed;
  top: var(--topbar-height);
  left: calc(-1 * var(--sidebar-width));
  width: var(--sidebar-width);
  height: calc(100% - var(--topbar-height));
  transition: transform var(--transition-speed);
  z-index: var(--z-index-sidebar);
}

.sidebar-container.sidebar-open,
.sidebar-container.toc-open {
  transform: translateX(var(--sidebar-width));
}

.sidebar {
  height: 100%;
  width: 100%;
  background-color: var(--background-color-secondary);
  overflow-y: auto;
}

nav ul, .toc-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

nav ul li a, .toc-list a {
  display: flex;
  align-items: center;
  padding: var(--spacing-small) var(--spacing-medium);
  color: var(--text-color);
  text-decoration: none;
}

nav ul li a:hover,
nav ul li a.active,
.toc-list a:hover {
  background-color: var(--highlight-color);
  color: var(--background-color);
}

.nav-icon {
  width: 20px;
  margin-right: var(--spacing-small);
}

@media (max-width: 768px) {
  .sidebar-container {
    width: 100%;
    left: -100%;
  }

  .sidebar-container.sidebar-open,
  .sidebar-container.toc-open {
    transform: translateX(100%);
  }
}
</style>
