import MarkdownIt from 'markdown-it'

const customRenderer = new MarkdownIt({
  html: true,
  breaks: true,
  linkify: true
})


const preprocessLatex = (content) => {
  // Helper function to escape regex special characters
  const escapeRegExp = (string) => {
    return string.replace(/[.*+?^${}()|[\]\\]/g, '\\$&');
  };

  // Function to replace LaTeX delimiters
  const replaceDelimiters = (text, start, end, newStart, newEnd) => {
    const escapedStart = escapeRegExp(start);
    const escapedEnd = escapeRegExp(end);
    const regex = new RegExp(`${escapedStart}([\\s\\S]*?)${escapedEnd}`, 'g');
    return text.replace(regex, (_, content) => `${newStart}${content.trim()}${newEnd}`);
  };

  // Replace $$ ... $$ with \[ ... \] but preserve the delimiters
  content = replaceDelimiters(content, '$$', '$$', '\\[', '\\]');

  // Replace $ ... $ with \( ... \) but preserve the delimiters
  content = content.replace(/(?<![\\$])\$(?!\$)(.*?)(?<![\\$])\$(?!\$)/g, (match, p1) => `\\(${p1.trim()}\\)`);

  // Escape existing \[ and \] to prevent them from being processed by markdown-it
  content = content.replace(/\\\[/g, '\\\\[').replace(/\\\]/g, '\\\\]');

  // Escape existing \( and \) to prevent them from being processed by markdown-it
  content = content.replace(/\\\(/g, '\\\\(').replace(/\\\)/g, '\\\\)');

  // Escape underscores in LaTeX expressions
  content = content.replace(/(\\\\\[|\\\\\()[\s\S]*?(\\\\\]|\\\\\))/g, (match) => {
    return match.replace(/_/g, '\\_');
  });

  // Escape curly braces in LaTeX expressions
  content = content.replace(/(\\\\\[|\\\\\()[\s\S]*?(\\\\\]|\\\\\))/g, (match) => {
    return match.replace(/\{/g, '\\{').replace(/\}/g, '\\}');
  });

  return content;
};










export const renderMarkdown = (content) => {
  content = preprocessLatex(content);
  
  // Use a custom renderer for code blocks to prevent processing LaTeX inside them
  customRenderer.renderer.rules.fence = (tokens, idx) => {
    const token = tokens[idx];
    return `<pre><code>${token.content}</code></pre>`;
  };

  let renderedHtml = customRenderer.render(content);

  // Unescape the LaTeX delimiters after markdown rendering
  renderedHtml = renderedHtml.replace(/\\\\(\[|\]|\(|\))/g, '\\$1');

  // Unescape underscores and curly braces in LaTeX expressions
  renderedHtml = renderedHtml.replace(/(\\\[|\\\()[\s\S]*?(\\\]|\\\))/g, (match) => {
    return match.replace(/\\_/g, '_').replace(/\\{/g, '{').replace(/\\}/g, '}');
  });

  return renderedHtml;
};

export const generateToc = (content) => {
  const toc = []
  let currentTopLevel = null

  content.split('\n').forEach(line => {
    if (line.startsWith('## ')) {
      const text = line.substring(3).trim()
      const id = text.toLowerCase().replace(/\s+/g, '-').replace(/[^\w-]+/g, '')
      currentTopLevel = { id, text, children: [] }
      toc.push(currentTopLevel)
    } else if (line.startsWith('### ') && currentTopLevel) {
      const text = line.substring(4).trim()
      const id = text.toLowerCase().replace(/\s+/g, '-').replace(/[^\w-]+/g, '')
      currentTopLevel.children.push({ id, text })
    }
  })

  return toc
}
