<template>
  <div v-if="isLoading" class="loading">Loading blog post...</div>
  <div v-else-if="error" class="error">{{ error }}</div>
  <div v-else-if="post" class="blog-post">
    <div class="blog-post-header">
      <h1 ref="titleElement" :class="{ 'hidden': isTitleHidden }">{{ post.title }}</h1>
      <p class="subtitle">{{ post.subtitle }}</p>
      <hr class="thin-line">
      <div class="metadata">
        <div v-if="post.authors" class="metadata-item">
          <strong>Authors:</strong> {{ post.authors.join(', ') }}
        </div>
        <div v-if="post.date" class="metadata-item">
          <strong>Published:</strong> {{ formatDate(post.date) }}
        </div>
      </div>
      <hr class="thin-line">
    </div>

    <div v-if="post.detailLevels && post.detailLevels.length > 1" class="detail-level-container">
      <span class="detail-label">Details slider:</span>
      <div class="slider-container">
        <input 
          type="range" 
          :min="0" 
          :max="post.detailLevels.length - 1" 
          v-model="detailLevel"
          class="slider"
        >
        <div class="slider-progress" :style="{ width: `${(parseInt(detailLevel) + 1) * (100 / post.detailLevels.length)}%` }"></div>
      </div>
      <span class="detail-level-label">{{ post.detailLevels[detailLevel] }}</span>
    </div>

    <div ref="postContent" v-html="renderedContent" class="post-content"></div>

    <div class="citation-section">
      <hr class="thin-line">
      <h3>Cite this article</h3>
      <div class="tabs-container">
        <button 
          v-for="tab in citationTabs" 
          :key="tab.key"
          @click="currentCitationTab = tab.key"
          :class="['tab-button', { active: currentCitationTab === tab.key }]"
        >
          {{ tab.label }}
        </button>
      </div>
      <div class="citation-content">
        <div v-if="currentCitationTab === 'bibtex'">
          <pre>{{ generateBibTeX() }}</pre>
        </div>
        <div v-else-if="currentCitationTab === 'apa'">
          <p>{{ generateAPA() }}</p>
        </div>
        <div v-else-if="currentCitationTab === 'mla'">
          <p>{{ generateMLA() }}</p>
        </div>
      </div>
    </div>
  </div>
  <div v-else class="error">Blog post not found.</div>
</template>

<script>
import { ref, computed, watch, onMounted, onUnmounted, nextTick } from 'vue'
import { useStore } from 'vuex'
import { useRoute } from 'vue-router'
import { useMathJax } from '@/composables/useMathJax'
import { renderMarkdown, generateToc } from '@/utils/markdownRenderer'

export default {
  name: 'BlogPost',
  setup() {
    const store = useStore()
    const route = useRoute()

    const { isMathJaxLoaded, renderMathInElement } = useMathJax()
    const post = ref(null)
    const postContent = ref(null)
    const titleElement = ref(null)
    const renderedContent = ref('')
    const isLoading = ref(true)
    const error = ref(null)
    const detailLevel = ref(0)
    const isTitleHidden = ref(false)
    const currentCitationTab = ref('bibtex')

    const isTocVisible = computed(() => store.state.content.tocSidebarOpen)

    const citationTabs = [
      { key: 'bibtex', label: 'BibTeX' },
      { key: 'apa', label: 'APA' },
      { key: 'mla', label: 'MLA' },
    ]

const loadBlogPost = async (slug) => {
  try {
    let response;
    if (process.env.NODE_ENV === 'production') {
      response = await fetch('/personal_website/assets/blog.json');
      response = await response.json();
    } else {
      response = await import('@/assets/blog.json');
      response = response.default;
    }

    const data = response;
    const foundPost = data.posts.find(p => p.slug === slug);

    if (foundPost) {
      post.value = foundPost;
      let markdownResponse;
      if (process.env.NODE_ENV === 'production') {
        markdownResponse = await fetch(`/personal_website/assets/blog/${slug}/${slug}.md`);
      } else {
        markdownResponse = await import(`@/assets/blog/${slug}/${slug}.md`);
      }
      const markdownContent = process.env.NODE_ENV === 'production' ? await markdownResponse.text() : markdownResponse.default;
      
      const toc = generateToc(markdownContent);
      store.dispatch('content/setTableOfContents', toc);
      renderedContent.value = renderMarkdown(markdownContent);
      
      store.dispatch('content/setCurrentBlogPostTitle', foundPost.title);

      nextTick(() => {
        if (postContent.value && isMathJaxLoaded.value) {
          renderMathInElement(postContent.value);
        }
      });
    } else {
      error.value = 'Blog post not found';
    }
  } catch (err) {
    console.error('Failed to load blog post:', err);
    error.value = 'Failed to load blog post. Please try again later.';
  } finally {
    isLoading.value = false;
  }
};
    const formatDate = (dateString) => {
      return new Date(dateString).toLocaleDateString('en-US', {
        year: 'numeric',
        month: 'long',
        day: 'numeric'
      })
    }

    const handleScroll = () => {
      if (titleElement.value) {
        const rect = titleElement.value.getBoundingClientRect()
        isTitleHidden.value = rect.bottom <= parseInt(getComputedStyle(document.documentElement).getPropertyValue('--topbar-height'))
        store.dispatch('content/setShowTitleInTopBar', isTitleHidden.value)
      }
    }

    onMounted(() => {
      loadBlogPost(route.params.slug)
      window.addEventListener('scroll', handleScroll, { passive: true })
    })

    onUnmounted(() => {
      window.removeEventListener('scroll', handleScroll)
    })

    watch(isMathJaxLoaded, (newValue) => {
      if (newValue && postContent.value) {
        renderMathInElement(postContent.value)
      }
    })

    watch(detailLevel, () => {
      if (post.value) {
        loadBlogPost(post.value.slug).then(() => {
          if (postContent.value && isMathJaxLoaded.value) {
            renderMathInElement(postContent.value);
          }
        });
      }
    });

    const generateBibTeX = () => {
      if (!post.value) return ''
      const authors = post.value.authors ? post.value.authors.join(' and ') : 'Unknown'
      const year = post.value.date ? new Date(post.value.date).getFullYear() : 'n.d.'
      return `@article{${post.value.slug},
  title = {${post.value.title}},
  author = {${authors}},
  year = {${year}},
  journal = {Personal Blog},
  url = {${window.location.href}}
}`
    }

    const generateAPA = () => {
      if (!post.value) return ''
      const authors = post.value.authors ? post.value.authors.map(author => {
        const names = author.split(' ')
        return `${names[names.length - 1]}, ${names.slice(0, -1).map(n => n[0]).join('. ')}.`
      }).join(', ') : 'Unknown'
      const year = post.value.date ? new Date(post.value.date).getFullYear() : 'n.d.'
      return `${authors} (${year}). ${post.value.title}. Personal Blog. ${window.location.href}`
    }

    const generateMLA = () => {
      if (!post.value) return ''
      const authors = post.value.authors ? post.value.authors.map(author => {
        const names = author.split(' ')
        return `${names[names.length - 1]}, ${names.slice(0, -1).join(' ')}`
      }).join(', ') : 'Unknown'
      const date = post.value.date ? new Date(post.value.date).toLocaleDateString('en-US', {day: 'numeric', month: 'short', year: 'numeric'}) : 'n.d.'
      return `${authors}. "${post.value.title}." Personal Blog, ${date}, ${window.location.href}.`
    }

    return {
      post,
      renderedContent,
      formatDate,
      isLoading,
      error,
      isTocVisible,
      detailLevel,
      postContent,
      titleElement,
      isTitleHidden,
      generateBibTeX,
      generateAPA,
      generateMLA,
      citationTabs,
      currentCitationTab
    }
  }
}
</script>

<style scoped>
