  display: flex;
  justify-content: space-between;
  color: var(--text-color-secondary);
  font-size: 0.9rem;
}

.detail-level-container {
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
}

.detail-label {
  margin-right: 1rem;
}

.slider-container {
  position: relative;
  width: 200px;
  height: 5px;
  background: #d3d3d3;
  border-radius: 5px;
}

.slider {
  -webkit-appearance: none;
  width: 100%;
  height: 5px;
  background: transparent;
  outline: none;
  margin: 0;
  z-index: 2;
  position: relative;
}

.slider-progress {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  background: var(--highlight-color);
  border-radius: 5px;
  pointer-events: none;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background: var(--highlight-color);
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background: var(--highlight-color);
  cursor: pointer;
}

.detail-level-label {
  margin-left: 1rem;
  font-size: 0.9rem;
  color: var(--text-color-secondary);
}

