// src/store/modules/content.js
export default {
  namespaced: true,
  state: () => ({
    sidebarOpen: false,
    tocSidebarOpen: false,
    topBarHidden: false,
    currentBlogPostTitle: '',
    tableOfContents: [],
    showTitleInTopBar: false
  }),
  mutations: {
    toggleSidebar(state) {
      state.sidebarOpen = !state.sidebarOpen
      if (state.sidebarOpen) {
        state.tocSidebarOpen = false
      }
    },
    toggleTocSidebar(state) {
      state.tocSidebarOpen = !state.tocSidebarOpen
      if (state.tocSidebarOpen) {
        state.sidebarOpen = false
      }
    },
    setSidebarOpen(state, value) {
      state.sidebarOpen = value
      if (value) {
        state.tocSidebarOpen = false
      }
    },
    setTocSidebarOpen(state, value) {
      state.tocSidebarOpen = value
      if (value) {
        state.sidebarOpen = false
      }
    },
    setTopBarHidden(state, value) {
      state.topBarHidden = value
    },
    setCurrentBlogPostTitle(state, title) {
      state.currentBlogPostTitle = title
    },
    setTableOfContents(state, toc) {
      state.tableOfContents = toc
    },
    setShowTitleInTopBar(state, value) {
      state.showTitleInTopBar = value
    }
  },
actions: {
    toggleSidebar({ commit, state }) {
      commit('setSidebarOpen', !state.sidebarOpen)
      if (state.tocSidebarOpen) {
        commit('setTocSidebarOpen', false)
      }
      commit('setTopBarHidden', false)
    },
    toggleTocSidebar({ commit, state }) {
      commit('setTocSidebarOpen', !state.tocSidebarOpen)
      if (state.sidebarOpen) {
        commit('setSidebarOpen', false)
      }
      commit('setTopBarHidden', false)
    },
    setSidebarOpen({ commit }, value) {
      commit('setSidebarOpen', value)
      if (value) {
        commit('setTopBarHidden', false)
      }
    },
    setTocSidebarOpen({ commit }, value) {
      commit('setTocSidebarOpen', value)
      if (value) {
        commit('setTopBarHidden', false)
      }
    },
    setTopBarHidden({ commit }, value) {
      commit('setTopBarHidden', value)
    },
    setCurrentBlogPostTitle({ commit }, title) {
      commit('setCurrentBlogPostTitle', title)
    },
    setTableOfContents({ commit }, toc) {
      commit('setTableOfContents', toc)
    },
    setShowTitleInTopBar({ commit }, value) {
      commit('setShowTitleInTopBar', value)
    }
  },
  getters: {
    isSidebarOpen: state => state.sidebarOpen,
    isTocSidebarOpen: state => state.tocSidebarOpen,
    isTopBarHidden: state => state.topBarHidden,
    getCurrentBlogPostTitle: state => state.currentBlogPostTitle,
    getTableOfContents: state => state.tableOfContents,
    isShowTitleInTopBar: state => state.showTitleInTopBar
  }
}
