// src/composables/useMathJax.js
import { ref, onMounted } from 'vue'

export function useMathJax() {
  const isMathJaxLoaded = ref(false)

  const loadMathJax = () => {
    return new Promise((resolve) => {
      window.MathJax = {
        tex: {
          inlineMath: [['\\(', '\\)']],
          displayMath: [['\\[', '\\]']],
          processEscapes: true,
          processEnvironments: true
        },
        options: {
          skipHtmlTags: ['script', 'noscript', 'style', 'textarea', 'pre', 'code']
        },
        svg: {
          fontCache: 'global'
        },
        startup: {
          pageReady: () => {
            isMathJaxLoaded.value = true
            resolve()
          }
        }
      }

      const script = document.createElement('script')
      script.src = 'https://cdn.jsdelivr.net/npm/mathjax@3/es5/tex-svg.js'
      script.async = true
      document.head.appendChild(script)
    })
  }

  const renderMathInElement = (element) => {
    if (isMathJaxLoaded.value && window.MathJax && window.MathJax.typesetPromise) {
      return window.MathJax.typesetPromise([element])
    } else {
      console.warn('MathJax not loaded yet')
      return Promise.resolve()
    }
  }

  onMounted(() => {
    if (!window.MathJax) {
      loadMathJax()
    } else {
      isMathJaxLoaded.value = true
    }
  })

  return {
    isMathJaxLoaded,
    renderMathInElement
  }
}
