<!-- src/components/TopBar.vue -->
<template>
  <header class="top-bar">
    <div class="top-bar-content">
      <button class="menu-button" @click="toggleSidebar">
        <font-awesome-icon :icon="['fas', isSidebarOpen ? 'times' : 'bars']" />
      </button>
      <h1 class="page-title" :class="{ 'visible': currentSectionTitle || pageTitle }">
        {{ currentSectionTitle || pageTitle }}
      </h1>
      <button v-if="isBlogPost" class="toc-button" @click="toggleTocSidebar">
        <font-awesome-icon :icon="['fas', isTocSidebarOpen ? 'times' : 'list']" />
      </button>
    </div>
  </header>
</template>

<script>
import { useStore } from 'vuex'
import { computed, ref, onMounted, onUnmounted } from 'vue'
import { useRoute } from 'vue-router'

export default {
  name: 'TopBar',
  setup() {
    const store = useStore()
    const route = useRoute()

    const currentSectionTitle = ref('')
    const isSidebarOpen = computed(() => store.state.content.sidebarOpen)
    const isTocSidebarOpen = computed(() => store.state.content.tocSidebarOpen)
    const isBlogPost = computed(() => route.name === 'BlogPost')

    const pageTitle = computed(() => {
      if (route.name === 'BlogPost') {
        return store.state.content.currentBlogPostTitle || 'Blog Post'
      }
      return route.meta.title || ''
    })

    const sectionTitles = ref([])

    const updateSectionTitles = () => {
      sectionTitles.value = Array.from(document.querySelectorAll('h1, h2')).map(el => ({
        element: el,
        text: el.textContent,
        top: el.getBoundingClientRect().top + window.pageYOffset
      }))
    }

    const handleScroll = () => {
      const scrollTop = window.pageYOffset || document.documentElement.scrollTop
      
      if (scrollTop > 100) {
        const currentSection = sectionTitles.value.reduce((prev, current) => {
          return (Math.abs(current.top - scrollTop) < Math.abs(prev.top - scrollTop) ? current : prev)
        }, sectionTitles.value[0] || { text: '' })
        currentSectionTitle.value = currentSection.text
      } else {
        currentSectionTitle.value = ''
      }
    }

    onMounted(() => {
      updateSectionTitles()
      window.addEventListener('scroll', handleScroll, { passive: true })
      handleScroll()
    })

    onUnmounted(() => {
      window.removeEventListener('scroll', handleScroll)
    })

    const toggleSidebar = () => {
      store.dispatch('content/toggleSidebar')
    }

    const toggleTocSidebar = () => {
      store.dispatch('content/toggleTocSidebar')
    }

    return {
      currentSectionTitle,
      pageTitle,
      isSidebarOpen,
      isTocSidebarOpen,
      toggleSidebar,
      toggleTocSidebar,
      isBlogPost
    }
  }
}
</script>

<style scoped>
.top-bar {
  height: var(--topbar-height);
  background-color: var(--topbar-background);
  border-bottom: 1px solid var(--border-color);
  z-index: 1000;
}

.top-bar-content {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 var(--spacing-medium);
}

.menu-button, .toc-button {
  background: none;
  border: none;
  font-size: 1.2rem;
  color: var(--text-color);
  cursor: pointer;
}

.page-title {
  margin: 0;
  font-size: 1.2rem;
  opacity: 0;
  transition: opacity 0.3s ease;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 60%;
}

.page-title.visible {
  opacity: 1;
}
</style>
