<template>

	<h1>Blog</h1>
	<p> None of the stuff on this page is complete or accurate. I am basically just building out the functionality of the site in the direction I would want it to go, then later I will populate it out with the actual conent.

This page will probably contain a list of my publish notes that I take for myself to learn things. I basically have always written as if I  am explaining something to a future version of myself, so I though "hey might as well publish some of this" (with a lot of kicks in the back from friends).

There is no comment system yet, and there probably wont be. Just use something from the contacts page if you have something really urgent to say about something that i've written.

Else just join me on my learning journey.

</p>

  <div class="blog content-container">
    <div class="controls">
      <div class="sort-options">
        <span>Sort:</span>
        <button 
          v-for="option in sortOptions" 
          :key="option.value"
          @click="currentSort = option.value"
          :class="{ active: currentSort === option.value }"
        >
          <i :class="['fas', option.icon]"></i> {{ option.label }}
        </button>
      </div>
      <ViewSelector v-model="currentView" />
    </div>

    <div v-if="isLoading" class="loading">Loading blog posts...</div>
    <div v-else-if="error" class="error">{{ error }}</div>
    <div v-else :class="['blog-list', viewClass]">
      <BlogItem
        v-for="post in sortedBlogPosts"
        :key="post.slug"
        :post="post"
        :currentView="currentView"
        @click="navigateToBlogPost(post.slug)"
      />
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted } from 'vue'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
import { useViewSelector } from '@/composables/useViewSelector'
import ViewSelector from '@/components/ViewSelector.vue'
import BlogItem from '@/components/BlogItem.vue'

export default {
  name: 'Blog',
  components: { ViewSelector, BlogItem },
  setup() {
    const store = useStore()
    const router = useRouter()
    const { currentView, viewClass } = useViewSelector('compact')

    const currentSort = ref('date')
    const isLoading = ref(true)
    const error = ref(null)

    const sortOptions = [
      { value: 'date', label: 'Date', icon: 'fa-calendar-alt' },
      { value: 'title', label: 'Title', icon: 'fa-sort-alpha-down' },
    ]

    const loadBlogPosts = async () => {
      try {
        await store.dispatch('blog/loadBlogPosts')
        isLoading.value = false
      } catch (err) {
        console.error('Failed to load blog posts:', err)
        error.value = 'Failed to load blog posts. Please try again later.'
        isLoading.value = false
      }
    }

    onMounted(loadBlogPosts)

    const blogPosts = computed(() => store.getters['blog/getBlogPosts'])

    const sortedBlogPosts = computed(() => {
      if (!blogPosts.value) return []
      return [...blogPosts.value].sort((a, b) => {
        switch (currentSort.value) {
          case 'date':
            return new Date(b.date) - new Date(a.date)
          case 'title':
            return a.title.localeCompare(b.title)
          default:
            return 0
        }
      })
    })

    const navigateToBlogPost = (slug) => {
      router.push({ name: 'BlogPost', params: { slug } })
    }

    return {
      currentView,
      viewClass,
      currentSort,
      sortOptions,
      sortedBlogPosts,
      navigateToBlogPost,
      isLoading,
      error
    }
  }
}
</script>

