// src/store/modules/blog.js
import blogData from '@/assets/blog.json'

const parseContent = (content) => {
  const levels = [[], [], []];
  let currentLevel = 0;
  
  content.split('\n').forEach(line => {
    if (line.startsWith('<!-- level: ')) {
      currentLevel = parseInt(line.match(/\d+/)[0]) - 1;
    } else {
      levels[currentLevel].push(line);
    }
  });
  
  return levels.map(level => level.join('\n'));
};

export default {
  namespaced: true,
  state: () => ({
    blogPosts: [],
    currentBlogPost: null,
    currentDetailLevel: 0,
  }),
  mutations: {
    setBlogPosts(state, posts) {
      state.blogPosts = posts
    },
    setCurrentBlogPost(state, post) {
      state.currentBlogPost = post
    },
    setCurrentDetailLevel(state, level) {
      state.currentDetailLevel = level
    }
  },
  actions: {
    async loadBlogPosts({ commit }) {
      commit('setBlogPosts', blogData.posts)
    },
    async setCurrentBlogPost({ commit, state }, slug) {
      const post = state.blogPosts.find(post => post.slug === slug)
      if (post) {
        try {
          const module = await import(`@/assets/blog/${slug}/${slug}.md`)
          const content = module.default
          post.contentLevels = parseContent(content)
          commit('setCurrentBlogPost', post)
        } catch (error) {
          console.error(`Error loading blog post content for ${slug}:`, error)
          commit('setCurrentBlogPost', null)
        }
      } else {
        commit('setCurrentBlogPost', null)
      }
    },
    updateDetailLevel({ commit }, level) {
      commit('setCurrentDetailLevel', level)
    }
  },
  getters: {
    getBlogPosts: state => state.blogPosts,
    getCurrentBlogPost: state => state.currentBlogPost,
    getCurrentDetailLevel: state => state.currentDetailLevel,
    getVisibleContent: (state) => {
      if (!state.currentBlogPost || !state.currentBlogPost.contentLevels) return ''
      
      return state.currentBlogPost.contentLevels
        .slice(0, state.currentDetailLevel + 1)
        .join('\n\n')
    }
  }
}
